@media screen and (max-width: 820px) {
  body {
    padding: 0;
  }

  .App {
    border-radius: 0;
  }

  .app-wrapper-content {
    position: relative;
  }

  .blured {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
