.catalog_item {
  padding: 5px;
  width: 24%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  transition: 0.3s;
  margin-top: 10px;
}

.catalog_item_block {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.image {
  margin-bottom: 20px;
  max-width: 75%;
}

.name {
  font-family: 'Jost', sans-serif;
  font-size: 1.3em;
  font-weight: 800;
  margin-bottom: 10px;
}

.description {
  margin-bottom: 10px;
  font-family: 'Jost', sans-serif;
  font-size: 0.9em;
  font-weight: 300;
}

.name_holder {
  display: flex;
  align-items: center;
  height: 50px;
}

.size {
  width: 100%;
  display: flex;
  padding-top: 15px;
}

.size_item {
  width: 34%;
  font-family: 'Jost', sans-serif;
  font-size: 1em;
  font-weight: 600;
  border: 0.5px solid #ddd;
  border-radius: 5px;
  padding: 5px 8px;
}

.packing_item {
  width: 100%;
  font-family: 'Jost', sans-serif;
  font-size: 0.8em;
  font-weight: 500;
  border: 0.5px solid #ddd;
  border-radius: 5px;
  padding: 5px 8px;
  background-color: #eee;
}

.size_item:hover {
  cursor: pointer;
}

.size_item:focus {
  background-color: #eee;
}

.price_holder {
  padding-top: 15px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.price {
  font-size: 22px;
}

.price_count {
  max-width: 55px;
}

.btn_order {
  padding: 8px 18px;
  background-color: #fe5f1e;
  color: #fff;
  font-family: 'Jost', sans-serif;
  font-weight: 400;
  font-size: 1em;
  border-radius: 30px;
}

.btn_order:hover {
  color: #fff;
  background-color: #ff8c5e;
}

.btn_order:focus {
  border: 1px solid #fe5f1e;
  background-color: #fff;
  color: #fe5f1e;
}

button {
  font-size: 13px;
  background-color: #fff;
  border: 0.5px solid #ddd;
  border-radius: 5px;
  padding: 5px 8px;
  transition: 0.2s;
}

button:hover {
  cursor: pointer;
  background-color: #ff8c5e;
}

.size_item:hover {
  background-color: #f9fff8;
}

button:focus {
  outline: none;
}

.order_index {
  padding: 5px 10px;
  border-radius: 50%;
  background-color: #fe5f1e;
  color: #fff;
}

@media (prefers-color-scheme: dark) {
  span,
  b {
    color: #ccc;
  }

  .size_item,
  .packing_item {
    background-color: #39383d;
    /* border: none; */
    border: 0.5px solid #2c2b2f;
    color: #fff;
  }

  .size_item {
    border: none;
  }

  .size_item:hover,
  .size_item:focus {
    background-color: rgb(177, 165, 165);
  }

  .btn_order {
    border: none;
  }
}

